@import url('https://fonts.googleapis.com/css2?family=Silkscreen&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital@1&family=Sora:wght@100&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');/* General CSS */
html {
  scroll-behavior: smooth;
}

a {
  color: inherit;
}

a:hover {
  cursor: pointer;
}

body {
  font-family: 'Sora', sans-serif;
  background-color: #F9F9F9;
  margin: 0;
  transition: 1s;
}

p {
  font-family: 'Nunito', sans-serif;}

input {
  width: 100%;
  margin: 8px 0;
  background-color: inherit;
  border-right: none;
  border-left: none;
  border-top: none;
  border-color: black;
  border-bottom: solid 1px inherit;
  color: inherit;
  text-align: left;
}

textarea {
  width: 95%;
  border-color: inherit;
  border-right: none;
  border-left: none;
  border-top: none;
  color: inherit;
  border-bottom: solid 1px inherit;
  padding: 12px 20px;
  height: 200px;
  background: inherit;
  margin-bottom: 4%;

}

li:hover {
  cursor: pointer;
  transition: 0.5s;
  color: #0002A1;
}
button {
  border: none;
  background-color: inherit;
}

button:hover {
  cursor: pointer;
}
#header{font-family: 'Nunito', sans-serif; font-size: 5rem;}

/* Navigation section */

.links {
  list-style: none;
  display: inline;
  text-align: left;
  position: relative;
}

.links li {
  padding:0 20px 0 20px;
  float: left;
  display: inline;
  margin-left: 1%;
  margin-top: 1.5%;

}

.logo {
  position: relative;
  padding: 0;
  width: 80px;
  margin-left: 0;
  float: left;
}

/* Mobile CSS */
.hamburger {
  display: none;
  top: 0;
  height: 45px;
  position: absolute;
  width: 40px;
}
.webnav{
  box-shadow: 2px 5px 5px black;
  height: 80px;
  width: 100%;
  position: absolute;
}
.mobilenav{ 
  background-color:inherit;
  position: relative;
  width: 100%;
  box-shadow: 5px 2px 5px black;}
.mobile-nav {
  transition: 1s;
  display: none;
}
#close-menu{
  float: right;
  border: solid 2px white;
  border-radius: 30px;
  padding: 0 10px 0 10px;
  top: 5%;
  right:3%;
  position: absolute;
}
.show-mobile {
  display: inline;
  transition: 1s;
  text-align: center;
  list-style-type: none;
  background-color: #040303;
  font-size: 2rem;
  top: 0;
  position: absolute;
  padding: 0;
  left: 0;
  width: 100%;
  margin: 0;
  height: 100vh;
  color: white;
  opacity: 1;
  z-index: 2;
}

/* Greeting section */
.greeting {
  color: black;
  top: 0;
  height: 50vh;
  text-align: center;
  padding: 5%;

}

.tyler {
  width: 200px;
  margin: 5% auto 0 auto;
  border-radius: 50%;
}

/* Dark Mode CSS */
/* for the entire body */
.darkbody {
  background-color: #151515;
  color: #FAF7F0;
  transition: 1s;
}

.darkbody input {
  border-color: #1F8A70;
}

.darkbody .greeting {
  color: inherit;
}

/* for the darkmode button */
.Sun {
  color: orange
}

.Sun:hover {
  color: #F9D371
}

.Moon {
  color: #DDDDDD
}

.Moon:hover {
  color: #6B728E
}



.darkMode {
  background-color: inherit;
  border: none;
  position: absolute;
  top:0%;
  margin: 1.5% 95%;
}

.darkbody #navbar {
  transition: 1s;
  background-color: #151515;

}

.darkMode:hover {
  cursor: pointer;
}

/* for all the buttons on the document */
.darkbody button {
  background-color: inherit;
  color: inherit;
  box-shadow: 2px 2px 3px inherit;
}

.darkbody .project-button:hover {
  background-color: #1F8A70;
  color: #151515;
  transition: 1s;
  box-shadow: inherit;
}

/* for the submit button in the contact section */
.darkbody .SubmitBTN {
  background-color: inherit;
}

.darkbody .SubmitBTN:hover {
  background-color: #1F8A70;
  color: #151515;
  box-shadow: 1px 4px 4px #181823;
  transition: 1s;
}

/* About section */

.column {
  float: left;
  margin-left: 15%;
  z-index: -1;
}

.column2 {
  float: right;
  margin-right: 15%;
  padding: 2%;
}

.column,
.column2 {
  border-radius: 10px;
  width: 25%;
  padding: 2%;
  box-shadow: 5px 5px 5px #000000;

}

.transition {
  transition: 1s;
}

/* skill section */
.skill-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 100%;
}

.skill-card {
  height: 200px;
  width: 200px;
  margin: 1%;
  box-shadow: 5px 5px 5px #000000;
  border-radius: 10px;


}

.skill-img {
  width: 100px;
  background-color: inherit;
}





/* project section */
.each-section {
  color: inherit;
  padding: 2%;
  text-align: center;
  height: 500px;

}

.project-section {
  display: flex;
  height: 400px;
  flex-wrap: wrap;
  justify-content: center;
}

.project-card {
  height: 180px;
  width: 300px;
  margin: 1%;
  box-shadow: 5px 5px 5px #000000;
  border-radius: 10px;
  word-wrap: break-word;
  overflow: hidden;
  transition: 0.5s;
}

.project-card:hover {
  height: 450px;
  cursor: pointer;
  background-color: inherit;
  z-index: 2;
}

.project-img {
  width: 200px;
  background-color: inherit;
}

.project-button {
  transition: 1s;
  padding: 5%;
  margin-left: 4%;
  background-color: #362FD9;
  color: white;
  opacity: 0.5;
  border-radius: 4%;
}

.project-button:hover {
  box-shadow: 5px 5px darkgray;
  opacity: 1;
  cursor: pointer;
}

/* Contact Section */
#Contact {
  margin-top: 0;
}

.SubmitBTN {
  font-size: 1rem;
  padding: 2%;
  background-color: #362FD9;
  color: white;
  border: none;
  border-radius: 10px;
  opacity: 0.5;
}

.SubmitBTN:hover {
  cursor: pointer;
  opacity: 1;
  box-shadow: 1px 4px 3px darkgray;
}

/* Footer */
.socials {
  margin-top: 2%;
  text-align: center;
  padding: 2%;
  font-family: 'Silkscreen', arial;
  bottom: 0;
}

.icons {
  display: inline;
  padding: 2px;
  margin: 1%;
}

.icons:hover {
  cursor: pointer;
  color: #E8DFCA;
}

/* Mobile Styling */
@media screen and (max-width: 700px) {
  .webnav{height: 10px;}
  .column {
    width: 30%;
    overflow-x: hidden;
  }

  .greeting {
    margin-top: 10%;
  }

  .logo {
    display: none;
  }

  .hamburger {
    display: inline;
  }

  .hamburger:hover {
    cursor: pointer;
  }

  #Projects {
    margin-top: 220%;
  }

  #Contact {
    margin-top: 110%;
  }

  input {
    width: 90%;
  }

  #navbar .links {
    display: none;
  }
#header{font-size: 3rem;}
.darkMode{
  margin: 2% 90%
}
}